.boardContainer_inputs-input {
  display: flex;
}

.boardContainer_inputs-label {
  align-self: center;
  margin-right: 10px;
}
.boardContainer_inputs-checkbox {
  -webkit-appearance: none;
  outline: none;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #111;
  box-shadow: 0px 0px 12px 4px rgb(255 255 255 / 10%), 4px 8px 11px rgb(0 0 0),
    inset -1px -2px 7px rgb(255 255 255 / 10%), inset 4px 5px 6px rgb(0 0 0);
  transition: 0.5s;
  cursor: pointer;
}
.boardContainer_inputs-checkbox:checked {
  background: #63ff00;
}
.boardContainer_inputs-checkbox:checked:before {
  content: '';
  position: absolute;
  left: 12px;
  top: 5px;
  width: 10px;
  height: 20px;
  border-bottom: 5px solid #111;
  border-right: 5px solid #111;
  transform: rotate(45deg);
  transition: 0.5s;
}
