@import url(https://fonts.googleapis.com/css2?family=Original+Surfer&display=swap);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: white;
  text-align: center;
}

.board_cell {
  height: calc(1rem + 3vw);
  width: calc(1rem + 3vw);
  background-color: transparent;
  transition: 500ms;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .board_cell {
    height: calc(1rem + 8vw);
    width: calc(1rem + 8vw);
  }
}
.board_cell-lit {
  background-color: #fe731c;
  box-shadow: 0 0 5px #fe731c;
}

.board_cell-hint {
  background-color: teal;
  box-shadow: 0 0 5px teal;
}

* {
  font-family: 'Original Surfer', cursive;
  letter-spacing: 1px;
}
.wrapper {
  height: 90vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.boardContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.boardContainer_board {
  display: -webkit-inline-flex;
  display: inline-flex;
  box-shadow: 0px 2px 15px rgb(243 63 174);
}

td {
  border: 1px solid #1ba7fe;
}

table {
  border-collapse: collapse;
}

p {
  color: #fe731c;
  font-size: 2rem;
  letter-spacing: 1px;
}

.boardContainer_inputs {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

/* Neons */

.neonContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 1rem;
}

@font-face {
  font-family: neon;
  src: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/707108/neon.ttf);
}

.neonContainer_lights {
  margin: 1rem 1rem 0 0;
  font-family: neon;
  color: #fb4264;
  font-size: 3em;
  text-shadow: 0 0 3vw #f40a35;
}

.neonContainer_out {
  margin: 0 1rem 1rem 0;
  font-family: neon;
  color: #426dfb;
  font-size: 4em;
  text-shadow: 0 0 3vw #2356ff;
}

@media screen and (max-width: 600px) {
  .neonContainer_lights {
    font-size: 2em;
  }
  .neonContainer_out {
    font-size: 3em;
  }
}

.neonContainer_lights {
  animation: lights 1.5s ease infinite;
  -moz-animation: lights 1.5s ease infinite;
  -webkit-animation: lights 1.5s ease infinite;
}

@-webkit-keyframes lights {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

@keyframes lights {
  0%,
  100% {
    text-shadow: 0 0 1vw #fa1c16, 0 0 3vw #fa1c16, 0 0 10vw #fa1c16,
      0 0 10vw #fa1c16, 0 0 0.4vw #fed128, 0.5vw 0.5vw 0.1vw #806914;
    color: #fed128;
  }
  50% {
    text-shadow: 0 0 0.5vw #800e0b, 0 0 1.5vw #800e0b, 0 0 5vw #800e0b,
      0 0 5vw #800e0b, 0 0 0.2vw #800e0b, 0.5vw 0.5vw 0.1vw #40340a;
    color: #806914;
  }
}

.neonContainer_out {
  animation: out 2s linear infinite;
  -moz-animation: out 2s linear infinite;
  -webkit-animation: out 2s linear infinite;
  -o-animation: out 2s linear infinite;
}

@-webkit-keyframes out {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

@keyframes out {
  0%,
  100% {
    text-shadow: 0 0 1vw #1041ff, 0 0 3vw #1041ff, 0 0 10vw #1041ff,
      0 0 10vw #1041ff, 0 0 0.4vw #8bfdfe, 0.5vw 0.5vw 0.1vw #147280;
    color: #28d7fe;
  }
  50% {
    text-shadow: 0 0 0.5vw #082180, 0 0 1.5vw #082180, 0 0 5vw #082180,
      0 0 5vw #082180, 0 0 0.2vw #082180, 0.5vw 0.5vw 0.1vw #0a3940;
    color: #146c80;
  }
}

.boardContainer_inputs-input {
  display: -webkit-flex;
  display: flex;
}

.boardContainer_inputs-label {
  -webkit-align-self: center;
          align-self: center;
  margin-right: 10px;
}
.boardContainer_inputs-checkbox {
  -webkit-appearance: none;
  outline: none;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #111;
  box-shadow: 0px 0px 12px 4px rgb(255 255 255 / 10%), 4px 8px 11px rgb(0 0 0),
    inset -1px -2px 7px rgb(255 255 255 / 10%), inset 4px 5px 6px rgb(0 0 0);
  transition: 0.5s;
  cursor: pointer;
}
.boardContainer_inputs-checkbox:checked {
  background: #63ff00;
}
.boardContainer_inputs-checkbox:checked:before {
  content: '';
  position: absolute;
  left: 12px;
  top: 5px;
  width: 10px;
  height: 20px;
  border-bottom: 5px solid #111;
  border-right: 5px solid #111;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: 0.5s;
}

button {
  letter-spacing: 0.1em;
  font-size: 1em;
  background: rgb(24, 23, 23);
  width: 6em;
  height: 2em;
  margin-right: calc(1rem + 2vw);
  color: white;
  border-radius: 5px;
  outline: none;
  border: none;
  box-shadow: inset 0 0 0 0 rgb(255, 0, 68);
  transition: ease-out 0.5s;
  cursor: pointer;
}
.winButton {
  -webkit-align-self: center;
          align-self: center;
  margin: 0 1rem;
}

button:hover:not([disabled]) {
  box-shadow: inset -10em 0 0 0 rgb(255, 0, 68);
}

button:hover {
  box-shadow: inset 10em 0 0 0 rgb(255, 0, 68);
}

button:active {
  padding-top: 5px;
}

.App {
  text-align: center;
  max-width: 100vw;
  max-height: 100vh;
}

